.catalog-video {
  position: relative;
  width: 100%;
  max-width: 280px;
  margin: 0 auto;
}

.catalog-video__button {
  position: absolute;
  width: 48px;
  height: 54px;
  padding: 0;
  border: 0;
  top: 35%;
  left: 45%;
  cursor: pointer;
  z-index: -1;
}

.catalog-video__icon {
  content: "";
  position: absolute;
  width: 48px;
  height: 54px;
  background-image: url("../img/catalog/button-player.svg");
  background-position: center;
  background-repeat: no-repeat;
  top: 35%;
  left: 45%;
  cursor: pointer;
}

.catalog-video__button + .catalog-video__icon:hover {
  opacity: 0.8;
}

.catalog-video__button + .catalog-video__icon:active {
  opacity: 0.5;
}

.catalog-video__img {
  width: 100%;
}

@media (min-width: @tablet-width) {
  .catalog-video {
    max-width: 648px;
  }

  .catalog-video .catalog-video__button {
    top: 42%;
    left: 46%;
  }

  .catalog-video .catalog-video__icon {
    top: 42%;
    left: 46%;
  }
}

@media (min-width: @desctop-width) {
  .catalog-video {
    max-width: 800px;
  }

  .catalog-video .catalog-video__button {
    top: 45%;
    left: 47%;
  }

  .catalog-video .catalog-video__icon {
    top: 45%;
    left: 47%;
  }
}
